@import "./../components/default/default.scss";

.gallery__gallery-grid-blocks {
  display: grid;
  gap: 10px;
  .grid-row-1,
  .grid-row-2,
  .grid-row-3 {
    display: grid;
    gap: 10px;
  }

  .grid-row-1 {
    grid-template-columns: repeat(2, auto);
    justify-content: space-between;
    .item-gallery-grid-blocks {
      &:nth-child(1) {
        max-width: 739px;
        max-height: 556px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
          width: 100%;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(2) {
        max-width: 520px;
        max-height: 556px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
    }
    @media (max-width: $tablet) {
      grid-template-columns: repeat(1, auto);
    }
  }

  .grid-row-2 {
    grid-template-columns: 1fr;
    .item-gallery-grid-blocks {
      @media (max-width: $tablet) {
        max-width: none;
        height: 390px;
      }
      @media (max-width: $mobile) {
        height: 300px;
      }
    }
  }

  .grid-row-3 {
    grid-template-columns: repeat(3, auto);
    justify-content: space-between;
    .item-gallery-grid-blocks {
      &:nth-child(1) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(2) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
      &:nth-child(3) {
        max-width: 409px;
        max-height: 404px;
        @media (max-width: $tablet) {
          max-width: none;
          height: 390px;
        }
        @media (max-width: $mobile) {
          height: 300px;
        }
      }
    }
    @media (max-width: $tablet) {
      grid-template-columns: repeat(1, 100%);
    }
  }

  .grid-row-1 div,
  .grid-row-2 div,
  .grid-row-3 div {
    width: 100%;
    height: 100%;
  }
  .item-gallery-grid-blocks {
    overflow: hidden;
    width: 100%;
    height: 100%;
    &__picture-block {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}

