@import "./../../banner.scss";
@import "./../../info-page.scss";
@import "./../../text-block-with-swiper.scss";
@import "./../../statistics.scss";
@import "./../../news.scss";
@import "./../../gallery.scss";

.about-the-company {
  .text-block-with-swiper {
    &__container {
      .text-block-with-swiper__text-block {
        .text-block-with-swiper__title-h2.title-h2 {
          margin-top: 30px;
          letter-spacing: 0.01em;
          @media (max-width: $mobile) {
            margin-top: 20px;
          }
        }
        .text-block-with-swiper__only-text-block {
          margin-top: 50px;
          @media (max-width: $mobile) {
            margin-top: 20px;
          }
        }
        .text-block-with-swiper__text {
          margin-top: 31px;
          line-height: 24px;
          letter-spacing: 0.01em;
          @media (max-width: $mobile) {
            margin-top: 20px;
          }
        }
      }
      .text-block-with-swiper__swiper-block.eco-walnut-swiper-block {
        .swiper-wrapper.text-block-with-swiper__wrapper {
        }
        .buttons-block {
        }
        .swiper-slide.text-block-with-swiper__slide {
          img {
          }
        }
        .swiper-button-next.text-block-with-swiper__button-next {
        }
        .swiper-button-prev.text-block-with-swiper__button-prev {
        }
      }
    }
  }

  .hystory-company {
    &_background-color {
      background-color: #fbfbfb;
    }
    padding-top: 100px;
    padding-bottom: 90px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &__container {
      display: grid;
      grid-template-columns: repeat(3, auto);
      justify-content: space-between;
      @media (max-width: 1250px) {
        grid-template-columns: repeat(2, auto);
        row-gap: 20px;
      }
      @media (max-width: 800px) {
        grid-template-columns: repeat(1, 1fr);
        row-gap: 20px;
      }
      .hystory-company__img-block {
        margin-top: auto;
        margin-bottom: auto;
        max-width: 342px;
        max-height: 493px;
        height: 100%;
        width: 100%;
        img {
          height: 100%;
          width: 100%;
          object-fit: cover;
        }
        @media (max-width: 800px) {
          margin-left: auto;
          margin-right: auto;
        }
      }
      .hystory-company__text-with-button-block {
        padding: 34px 10px 40px 22px;
        max-width: 450px;
        flex: 0 0 100%;
        @media (max-width: 800px) {
          margin-left: auto;
          margin-right: auto;
        }
        @media (max-width: $mobile) {
          padding: 0px;
        }
        .hystory-company__owner-info {
        }
        .rectangle.unbackground {
          margin-top: 60px;
          padding: 16px 42px 16px 42px;
          border-width: 2px;
          display: block;
          margin-left: 0;
          @media (max-width: $mobile) {
            margin-left: auto;
            margin-right: auto;
            margin-top: 20px;
          }
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
        .hystory-company__owner-name {
          @include rale-800-32;
        }
        .hystory-company__descrition {
          margin-top: 17px;
          @include rale-500-16;
          line-height: 20px;
          max-width: 290px;
        }
        .hystory-company__quote {
          margin-top: 36px;
          position: relative;
          padding-left: 38px;
          @include rale-500-15-i;
          line-height: 20px;
          letter-spacing: 0.08em;
          &::before {
            position: absolute;
            left: 0;
            margin-right: 10px;
            content: "\e928";
            font-family: "icomoon";
            font-size: 19px;
            color: $darkGreen;
          }
          @media (max-width: $mobile) {
            margin-top: 20px;
          }
        }
      }
      .hystory-company__text-block {
        overflow: hidden;
        position: relative;
        background-color: $darkGreen;
        max-width: 453px;
        flex: 0 0 100%;
        padding: 34px 34px 43px 47px;
        @media (max-width: 1250px) {
          max-width: none;
          grid-column-start: 1;
          grid-column-end: 3;
          @media (max-width: 800px) {
            grid-column-start: 1;
            grid-column-end: 2;
          }
        }
        @media (max-width: $mobile) {
          padding: 20px 20px 20px 20px;
        }
        img {
          position: absolute;
          bottom: -23px;
          right: -88px;
        }
        .hystory-company__title-h3.title-h3 {
          @include rale-800-32;
          color: $white;
          display: block;
          margin-bottom: 34px;
        }

        .hystory-company__text {
          color: $white;
          @include mont-500-14;
          line-height: 22px;
          margin-top: 15px;
        }
      }
    }
  }

  .gallery-title-button-section {
    padding-top: 90px;
    padding-bottom: 100px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    &_background-color {
      background-color: #fbfbfb;
    }
    &__container {
      .gallery-title-button-section__title-block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .gallery-title-button-section__title {
          @include rale-800-45;
          line-height: 53px;
        }
        .gallery-title-button-section__description {
          margin-top: 22px;
          @include rale-500-18;
        }
      }
      .gallery__gallery-grid-blocks {
        margin-top: 64px;
        @media (max-width: $tablet) {
          margin-top: 30px;
        }
        @media (max-width: $mobile) {
          margin-top: 20px;
        }
        .gallery-grid-block {
        }
        .img-block.img-block-small {
          img {
          }
        }
        .video-block.img-block-small {
          img {
          }
          .text-block {
            &__title {
            }
            &__text {
            }
            strong {
            }
          }
          .icon-play {
          }
        }
        .video-block {
          img {
          }
          .text-block {
            &__title {
            }
            &__text {
            }
            strong {
            }
          }
          .icon-play {
          }
        }
        .img-block.img-block-big {
          img {
          }
        }
        .img-text-block {
          img {
          }
          .text-block {
            &__title {
            }
            &__text {
            }
            br {
            }
          }
        }
      }
      .button.rectangle.unbackground {
        max-width: max-content;
        padding: 17px 33px;
        border-width: 2px;
        @media (max-width: $tablet) {
          margin-top: 30px;
        }
        @media (max-width: $mobile) {
          margin-top: 20px;
        }
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .news-block-swiper__container{
    padding-top: 90px;
    @media (max-width: $tablet) {
      padding-top: 45px;
      padding-bottom: 45px;
    }
    @media (max-width: $mobile) {
      padding-top: 30px;
      padding-bottom: 30px;
    }
    .swiper-news-block-swiper .swiper-wrapper.news-block-swiper__wrapper{
      margin-top: 68px;
      @media (max-width: $tablet) {
        margin-top: 30px;
      }
      @media (max-width: $mobile) {
        margin-top: 20px;
      }
    }
  }
}
